import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const Login = () => import('@/views/login/Login')
const Home = () => import('@/views/home/Home')
const Welcome = () => import('@/views/welcome/Welcome')
const Alluser = () => import('@/views/alluser/Alluser')
const Adduser = () => import('@/views/adduser/Adduser')
const Userdata = () => import('@/views/userdata/Userdata')

const routes = [
  {
    path: '/',
    redirect: '/login',
    component: Login
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/home',
    name: 'home',
    component: Home,
    redirect: '/welcome',
    children: [
      {
        path: '/welcome',
        component: Welcome
      },
      {
        path: '/alluser',
        component: Alluser
      },
      {
        path: '/adduser',
        component: Adduser
      },
      {
        path: '/userdata',
        component: Userdata
      }
    ]
  }
]

const router = new VueRouter({
  routes
})
// 路由守卫
router.beforeEach((to, from, next) => {
  if (to.path == '/login') {
    return next()
  }
  const token = window.sessionStorage.getItem('token')
  if (!token) {
    return next({ path: '/login' })
  }
  next()
})
export default router
